@import url(https://fonts.googleapis.com/css?family=Amatic+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.titletext{
  color: coral;
  font-family: 'Amatic SC', cursive;
  font-size: 700%;
  width: 72%;
  top: 30%;
  margin-left: 14%;
  background-color: darkolivegreen;
  background-size: .2% .1%;
  padding-top : .2% 0.5%;
}

.headertext {
  background-image: url(/oregano-online/static/media/spices_background.d9cce979.jpg);
  background-size: 100%;
  height: 960px;
  padding-top: 80px;
  background-repeat: no-repeat;
}

.button {
  background-color: darkolivegreen;
  border: none;
  color: beige;
  padding: 1% 2%;
  text-align: center;
  top: 30%;
  font-size: 50px;
  font-family: 'Amatic SC', cursive;
}

.buttons {
  margin-top: 30px;
}

.button:hover {
  background-color: darkgreen;
} 

.subheader_text {
  color: darkolivegreen;
  font-family: 'Amatic SC', cursive;
  font-size: 100px;
  height: 20%;
  padding-top: 40px;
  float: left;
  width: 100%;
  padding-left: 40px;
}

.searchbar {
  float: right;
  text-align: right;
  width: 100%;
}

.subheader {
  width: 98%;
}

.subtextContainer {
  height: 100px;
  margin-top: 1%;
}

.subpage {
  padding: 1%;
  margin: 1%;
}

.subtext {
  height: 100%;
  margin-top: 10px;
}

.subtextleft {
  background-color: darkolivegreen;
  color: beige;
  font-family: 'Amatic SC', cursive;
  font-size: 30px; 
  padding: 1%;
  float: left; 
  text-align: left;
  width: 48%;
}

.subtextright {
  background-color: darkolivegreen;
  color: beige;
  font-family: 'Amatic SC', cursive;
  font-size: 30px; 
  float: right; 
  padding: 1%;
  text-align: right;
  width: 48%;
}

.subtextright_advanced {
  text-align: right;
}

.checked {
  color: orange;
}

.modelBox {
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  position: fixed; 
  background-color: rgba(0, 0, 0, 0.425);
}

.innerModelBox {
  background-color: grey;
  width: 60%;
  height: 60%;
  position: fixed; 
  left: 20%;
  top: 20%;
}

.modelName {
  width: 100%;
  font-family: 'Amatic SC', cursive;
  color: beige;
  font-size: 200%;
  background-color: #707070;
}

.floatingLeft {
  margin-top: 20px;
  margin-left: 20px;
  float: left;
  font-family: 'Amatic SC', cursive;
  color: beige;
  font-size: 130%;
}
.Header {
    width: 100%;
    height: 60px;
    background-color: darkolivegreen;
    
    position: fixed;
    text-align: left;
    font-size: 300%;
    padding-left: 1%;
    
    left: 0px;
    top: 0px;
}

.Headertext {
    width: 80%;
    font-family: 'Amatic SC', 'cursive';
    color: beige;
    float: left;
}

.home_button_div {
    width: 19%;
    float: right;
    background-color: darkolivegreen;
    height: 60px;
    margin-top: 0px;
    margin-right: 1%;
}

.homebutton {
    font-family: 'Amatic SC', 'cursive';
    font-size: 42px;
    background-color: beige;
    color: darkolivegreen;
    float: right;
}

